@mixin fade-in {
    opacity: 1;
    animation-name: fade;
    animation-iteration-count: 1;
    animation-timing-function: ease-in;
    animation-duration: .6s; }

.wrapper {
    display: flex;
    flex-direction: column;
    @include fade-in; }



@keyframes fade {

    0% {
        opacity: 0; }

    100% {
        opacity: 1; } }
