.switch {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 14px;
    margin: 13px 10px;
    &__link {
        text-decoration: none; }
    &__btn {
        padding: 9px 6px;
        font-weight: 300;
        line-height: 24px;
        color: #7D7D7D;
        font-size: 20px;
        font-family: 'Balsamiq Sans', cursive;
        cursor: pointer;
        border-bottom: 2px solid transparent;
        text-decoration: none;
        transition: all .4s ease-out;
        &__active {
            animation-name: br;
            animation-iteration-count: 1;
            animation-timing-function: ease-in-out;
            animation-duration: .5s;
            text-decoration: none;
            color: #ffffff;
            border-bottom: 2px solid #0697f1;
            &__anime {
                opacity: 0;
                animation-name: br;
                animation-iteration-count: 1;
                animation-timing-function: ease-in-out;
                animation-duration: .5s;
                text-decoration: none;
                color: #ffffff;
                border-bottom: 2px solid #0697f1; } } } }

@keyframes br {
    0% {
        border-bottom: 2px solid transparent; }

    100% {
        border-bottom: 2px solid #0697f1; } }
